.header__img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  object-fit: cover;
  max-height: 825px;

  @media (max-width: $desktop) {
    max-height: 341px;
  }

  @media (max-width: $tablet) {
    max-height: 390px;
  }
}

.header__nav {
  margin-bottom: 255px;

  @media (max-width: $desktop) {
    margin-bottom: 0;
  }
}

.header__container {
  width: 920px;
  margin: 0 auto;
  padding: 0 20px;

  @media (max-width: $desktop) {
    width: 100%;
    padding: 0 21px;
  }

  @media (max-width: $tablet) {
    padding: 0 16px;
  }
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: stretch;

  @media (max-width: $desktop) {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}

.header__title {
  @include font(46px, 64px, 700);
  @include fields();

  @media (max-width: $desktop) {
    @include font(30px, 42px, 700);
    width: 70%;
    margin-bottom: 20px;
  }

  @media (max-width: $tablet) {
    width: 90%;
  }
}

.header__text {
  @include font(20px, 32px, 300);
  @include fields();
  opacity: 0.8;

  @media (max-width: $desktop) {
    @include font(18px, 32px, 300);
    width: 55%;
  }

  @media (max-width: $tablet) {
    width: 100%;
  }
}

.header__intro {
  width: 381px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (max-width: $desktop) {
    width: 100%;
    margin-top: 25px;
    min-height: 315px;
    margin-bottom: 70px;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
  }

  @media (max-width: $tablet) {
    min-height: 363px;
  }
}

.header__logo-link {
  @media (max-width: $desktop) {
    align-self: flex-start;
    margin-bottom: 80px;
  }

  @media (max-width: $tablet) {
    margin-bottom: 66px;
  }
}

.header__form {
  width: 327px;

  @media (max-width: $desktop) {
    width: 45%;
  }

  @media (max-width: $tablet) {
    width: 100%;
  }
}
